import React from "react";
import Chip from "@material-ui/core/Chip";
import withStyles from "@material-ui/core/styles/withStyles";
import PropTypes from "prop-types";
import { styles } from "./styles";

const AirportTag = ({ icon, classes, color, label }) => {
    const labelClasses = icon ? classes.hasIcon : classes.label;

    return (
        <Chip
            classes={{
                root: classes.root,
                icon: classes.icon,
                [labelClasses]: classes[labelClasses],
            }}
            label={label}
            className={classes[color]}
            icon={icon}
        />
    );
};

AirportTag.propTypes = {
    classes: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    label: PropTypes.string.isRequired,
    color: PropTypes.string.isRequired,
    icon: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
};

export default withStyles(styles, { withTheme: true })(AirportTag);
